import React, { useState } from 'react';
import DiceRoll from './diceRoll';

function Game() {
  const [roll, setRoll] = useState(0);

  function changeRoll() {
    if (roll < 3) {
      setRoll(roll + 1);
    } else {
      setRoll(0);
    }
  }
  return (
    <React.Fragment>
      <DiceRoll roll={roll} setRoll={setRoll} changeRoll={changeRoll}/>
    </React.Fragment>
  )
}

export default Game;
