import React, { useState} from 'react';
import Dice from './dice';
import ScoreCard from './scoreCard';
import Collapse from './collapse';

function DiceRoll(props) {

  //useState hook - set to array for Dice values
  const [diceValue, setDiceValue] = useState({
    redDie: 1,
    blueDie: 1,
    greenDie: 1,
    yellowDie: 1,
    purpleDie: 1,
  });

  const [diceToggle, setDiceToggle] = useState({
    redDieOn: false,
    blueDieOn: false,
    greenDieOn: false,
    yellowDieOn: false,
    purpleDieOn: false,
  });

  const [cardScore, setCardScore] = useState({
    sum1: 0,
    sum2: 0,
    sum3: 0,
    sum4: 0,
    sum5: 0,
    sum6: 0,
    bonus: 0,
    topTotal: 0,
    threeKind: 0,
    fourKind: 0,
    smallStraight: 0,
    largeStraight: 0,
    fullHouse: 0,
    twoPair: 0,
    yahtzee: 0,
    chance: 0,
    bonusYahtzee: 0,
    bottomTotal: 0,
    grandTotal: 0,
  });

  const [handsUsed, setHandsUsed] = useState ({
    sum1: false,
    sum2: false,
    sum3: false,
    sum4: false,
    sum5: false,
    sum6: false,
    chance: false,
    threeKind: false,
    fourKind: false,
    smallStraight: false,
    largeStraight: false,
    fullHouse: false,
    twoPair: false,
    yahtzee: false,
    bonusYahtzee: false,
  });

  /*Helper functions*/
  //Create array from the state objects
  const handsArray = Object.values(handsUsed);

  function countInArray(array, value) {
    return array.reduce((n, x) => n + (x === value), 0);
  }

  function newTurn(e) {
    e.preventDefault();
    setDiceValue({
      redDie: 1,
      blueDie: 1,
      greenDie: 1,
      yellowDie: 1,
      purpleDie: 1,
    });
    setDiceToggle ({
      redDieOn: false,
      blueDieOn: false,
      greenDieOn: false,
      yellowDieOn: false,
      purpleDieOn: false,
    });
    let die1 = document.getElementById('red');
    let die2 = document.getElementById('blue');
    let die3 = document.getElementById('green');
    let die4 = document.getElementById('yellow');
    let die5 = document.getElementById('purple');
    die1.classList.remove("toggled");
    die2.classList.remove("toggled");
    die3.classList.remove("toggled");
    die4.classList.remove("toggled");
    die5.classList.remove("toggled");
    props.setRoll(0);
  }

  function reset() {
    let checkboxes = document.getElementsByTagName('input');
    for (var i = 0; i < checkboxes.length; i++) {
        if (checkboxes[i].type == 'checkbox')
            checkboxes[i].checked = false;
    }
    setDiceValue({
      redDie: 1,
      blueDie: 1,
      greenDie: 1,
      yellowDie: 1,
      purpleDie: 1,
    });
    setDiceToggle ({
      redDieOn: false,
      blueDieOn: false,
      greenDieOn: false,
      yellowDieOn: false,
      purpleDieOn: false,
    });
    setCardScore ({
      sum1: 0,
      sum2: 0,
      sum3: 0,
      sum4: 0,
      sum5: 0,
      sum6: 0,
      bonus: 0,
      topTotal: 0,
      threeKind: 0,
      fourKind: 0,
      smallStraight: 0,
      largeStraight: 0,
      fullHouse: 0,
      twoPair: 0,
      yahtzee: 0,
      chance: 0,
      bonusYahtzee: 0,
      bottomTotal: 0,
      grandTotal: 0,
    });
    setHandsUsed ({
      sum1: false,
      sum2: false,
      sum3: false,
      sum4: false,
      sum5: false,
      sum6: false,
      chance: false,
      threeKind: false,
      fourKind: false,
      smallStraight: false,
      largeStraight: false,
      fullHouse: false,
      twoPair: false,
      yahtzee: false,
      bonusYahtzee: false,
    });
    let die1 = document.getElementById('red');
    let die2 = document.getElementById('blue');
    let die3 = document.getElementById('green');
    let die4 = document.getElementById('yellow');
    let die5 = document.getElementById('purple');
    die1.classList.remove("toggled");
    die2.classList.remove("toggled");
    die3.classList.remove("toggled");
    die4.classList.remove("toggled");
    die5.classList.remove("toggled");
    props.setRoll(0);
  }

  //function that calculates all of the score Totals
  function updateScore (e) {
    e.preventDefault();
    let singleSum = 0;
    singleSum = cardScore.sum1 + cardScore.sum2 + cardScore.sum3 + cardScore.sum4 + cardScore.sum5 + cardScore.sum6;

    let bonus = 0;
    if (singleSum >= 63) {
      bonus = 35;
    }
    const topTotal = singleSum + bonus;
    let bottomTotal = 0;

    bottomTotal = cardScore.threeKind +
      cardScore.fourKind +
      cardScore.smallStraight +
      cardScore.largeStraight +
      cardScore.fullHouse +
      cardScore.twoPair +
      cardScore.yahtzee +
      cardScore.chance +
      cardScore.bonusYahtzee;

    const grandTotal = topTotal + bottomTotal;

    setCardScore({
        ...cardScore,
        bonus: bonus,
        topTotal: topTotal,
        bottomTotal: bottomTotal,
        grandTotal: grandTotal,
    });
  }

  //function that takes values from dice roll and puts them in state
  function handleRoll(event) {
    event.preventDefault();

    const falseCount = countInArray(handsArray, false);

    if (falseCount === 0) {
      alert('Game Over - You must reset game');
      return;
    }

    if (props.roll < 3) {
      props.changeRoll();

      let d1 = Math.floor(Math.random() * 6) + 1;
      let d2 = Math.floor(Math.random() * 6) + 1;
      let d3 = Math.floor(Math.random() * 6) + 1;
      let d4 = Math.floor(Math.random() * 6) + 1;
      let d5 = Math.floor(Math.random() * 6) + 1;

      if (diceToggle.redDieOn === true) {
        d1 = diceValue.redDie
      };
      if (diceToggle.blueDieOn === true) {
        d2 = diceValue.blueDie
      };
      if (diceToggle.greenDieOn === true) {
        d3 = diceValue.greenDie
      };
      if (diceToggle.yellowDieOn === true) {
        d4 = diceValue.yellowDie
      }
      if (diceToggle.purpleDieOn === true) {
        d5 = diceValue.purpleDie
      };

      setDiceValue({
        redDie: d1,
        blueDie: d2,
        greenDie: d3,
        yellowDie: d4,
        purpleDie: d5,
      });

      let die1 = document.getElementById('red');
      let die2 = document.getElementById('blue');
      let die3 = document.getElementById('green');
      let die4 = document.getElementById('yellow');
      let die5 = document.getElementById('purple');

      if (diceToggle.redDieOn === false) {
        die1.classList.add("rolled");
      }
      if (diceToggle.blueDieOn === false) {
        die2.classList.add("rolled");
      }
      if (diceToggle.greenDieOn === false) {
        die3.classList.add("rolled");
      }
      if (diceToggle.yellowDieOn === false) {
        die4.classList.add("rolled");
      }
      if (diceToggle.purpleDieOn === false) {
        die5.classList.add("rolled");
      }

      setTimeout(function(){
        die1.classList.remove("rolled");
        die2.classList.remove("rolled");
        die3.classList.remove("rolled");
        die4.classList.remove("rolled");
        die5.classList.remove("rolled");
      }, 400);
    };
  }

  return (
    <React.Fragment id="board">
      <div id="left-half" className="column">
        <Collapse>
          <div id="instructions" className="content">
            <p>• To start game, Roll Dice</p>
            <p>• Click the dice that you'd like to "freeze", then roll again</p>
            <p>• After 3 rolls, click the checkbox that you'd like to use for scoring</p>
            <p>• At any time, click "Update Score Totals" to update score</p>
            <p>• After 3 rolls, click "New Turn" to start a new turn</p>
          </div>
        </Collapse>
        <Dice roll={props.roll} diceValue={diceValue} diceToggle={diceToggle} setDiceToggle={setDiceToggle}/>
        <div className="fiftygrid">
          <a id="roll-button" href="/#" onClick={handleRoll}>
            Roll Dice
          </a>
          <a id="reset-button" href="/#" onClick={newTurn}>
            New Turn
          </a>
        </div>
        <p className="which-roll center">Rolls this turn = {props.roll}</p>

        <a id="score-button" href="#" onClick={updateScore}>
          Update Score Totals
        </a>
        <ScoreCard roll={props.roll} diceValue={diceValue} cardScore={cardScore} handsUsed={handsUsed} setCardScore={setCardScore} setHandsUsed={setHandsUsed} updateScore={updateScore}/>
        <button id="reset-game" onClick={reset}>Reset Game</button>
      </div>
    </React.Fragment>
  );
}

export default DiceRoll;
