import React from 'react';

function Dice(props) {

  function setRed() {
    const die1 = document.getElementById('red');
    if (props.roll == 0) {
      return;
    }
    if (!die1.classList.contains("toggled")) {
      die1.classList.add("toggled")
    } else if (die1.classList.contains("toggled")) {
      die1.classList.remove("toggled");
    };
    const flipRed = !props.diceToggle.redDieOn;
    props.setDiceToggle({
      ...props.diceToggle,
      redDieOn: flipRed,
    });
  };

  function setBlue() {
    const die2 = document.getElementById('blue');
    if (props.roll == 0) {
      return;
    }
    if (!die2.classList.contains("toggled")) {
      die2.classList.add("toggled")
    } else if (die2.classList.contains("toggled")) {
      die2.classList.remove("toggled");
    };
    const flipBlue = !props.diceToggle.blueDieOn;
    props.setDiceToggle({
      ...props.diceToggle,
      blueDieOn: flipBlue,
    });
  };

  function setGreen() {
    const die3 = document.getElementById('green');
    if (props.roll == 0) {
      return;
    }
    if (!die3.classList.contains("toggled")) {
      die3.classList.add("toggled")
    } else if (die3.classList.contains("toggled")) {
      die3.classList.remove("toggled");
    };
    const flipGreen = !props.diceToggle.greenDieOn;
    props.setDiceToggle({
      ...props.diceToggle,
      greenDieOn: flipGreen,
    });
  };

  function setYellow() {
    const die4 = document.getElementById('yellow');
    if (props.roll == 0) {
      return;
    }
    if (!die4.classList.contains("toggled")) {
      die4.classList.add("toggled")
    } else if (die4.classList.contains("toggled")) {
      die4.classList.remove("toggled");
    };
    const flipYellow = !props.diceToggle.yellowDieOn;
    props.setDiceToggle({
      ...props.diceToggle,
      yellowDieOn: flipYellow,
    });
  };

  function setPurple() {
    const die5 = document.getElementById('purple');
    if (props.roll == 0) {
      return;
    }
    if (!die5.classList.contains("toggled")) {
      die5.classList.add("toggled")
    } else if (die5.classList.contains("toggled")) {
      die5.classList.remove("toggled");
    };
    const flipPurple = !props.diceToggle.purpleDieOn;
    props.setDiceToggle({
      ...props.diceToggle,
      purpleDieOn: flipPurple,
    });
  };

  const redDieValue =  props.diceValue.redDie;
  const blueDieValue =  props.diceValue.blueDie;
  const greenDieValue =  props.diceValue.greenDie;
  const yellowDieValue =  props.diceValue.yellowDie;
  const purpleDieValue =  props.diceValue.purpleDie;

  return (
    <div>
    <div id="dice-buttons">
      <button className="dice" id="red" onClick={setRed}>{ redDieValue }</button>
      <button className="dice" id="blue" onClick={setBlue}>{ blueDieValue }</button>
      <button className="dice" id="green" onClick={setGreen}>{ greenDieValue }</button>
      <button className="dice" id="yellow" onClick={setYellow}>{ yellowDieValue }</button>
      <button className="dice" id="purple" onClick={setPurple}>{ purpleDieValue }</button>
    </div>
    </div>
  );
};

export default Dice;
