import React from 'react';

function ScoreCard(props) {
  //Create array from the state objects
  const diceValuesArray = Object.values(props.diceValue);

  /*Helper functions*/
  function countInArray(array, value) {
    return array.reduce((n, x) => n + (x === value), 0);
  }

  /*Click actions*/
  function setOnes(){
    if (props.roll === 0) {
      return;
    }
    const oneCount = countInArray(diceValuesArray, 1);
    const oneScore = oneCount * 1;
    props.setCardScore({
      ...props.cardScore,
      sum1: oneScore,
    });
    props.setHandsUsed({
      ...props.handsUsed,
      sum1: true,
    });
    if (document.getElementById('sum1').checked === false) {
      props.setCardScore({
        ...props.cardScore,
        sum1: 0,
      });
      props.setHandsUsed({
        ...props.handsUsed,
        sum1: false,
      });
    };
  };

  function setTwos(){
    const twoCount = countInArray(diceValuesArray, 2);
    const twoScore = twoCount * 2;
    props.setCardScore({
      ...props.cardScore,
      sum2: twoScore,
    });
    props.setHandsUsed({
      ...props.handsUsed,
      sum2: true,
    });
    if (document.getElementById('sum2').checked === false) {
      props.setCardScore({
        ...props.cardScore,
        sum2: 0,
      });
      props.setHandsUsed({
        ...props.handsUsed,
        sum2: false,
      });
    };
  };

  function setThrees(){
    const threeCount = countInArray(diceValuesArray, 3);
    const threeScore = threeCount * 3;
    props.setCardScore({
      ...props.cardScore,
      sum3: threeScore,
    });
    props.setHandsUsed({
      ...props.handsUsed,
      sum3: true,
    });
    if (document.getElementById('sum3').checked === false) {
      props.setCardScore({
        ...props.cardScore,
        sum3: 0,
      });
      props.setHandsUsed({
        ...props.handsUsed,
        sum3: false,
      });
    };
  };

  function setFours(){
    const fourCount = countInArray(diceValuesArray, 4);
    const fourScore = fourCount * 4;
    props.setCardScore({
      ...props.cardScore,
      sum4: fourScore,
    });
    props.setHandsUsed({
      ...props.handsUsed,
      sum4: true,
    });
    if (document.getElementById('sum4').checked === false) {
      props.setCardScore({
        ...props.cardScore,
        sum4: 0,
      });
      props.setHandsUsed({
        ...props.handsUsed,
        sum4: false,
      });
    };
  };

  function setFives(){
    const fiveCount = countInArray(diceValuesArray, 5);
    const fiveScore = fiveCount * 5;
    props.setCardScore({
      ...props.cardScore,
      sum5: fiveScore,
    });
    props.setHandsUsed({
      ...props.handsUsed,
      sum5: true,
    });
    if (document.getElementById('sum5').checked === false) {
      props.setCardScore({
        ...props.cardScore,
        sum5: 0,
      });
      props.setHandsUsed({
        ...props.handsUsed,
        sum5: false,
      });
    };
  };

  function setSixes(){
    const sixCount = countInArray(diceValuesArray, 6);
    const sixScore = sixCount * 6;
    props.setCardScore({
      ...props.cardScore,
      sum6: sixScore,
    });
    props.setHandsUsed({
      ...props.handsUsed,
      sum6: true,
    });
    if (document.getElementById('sum6').checked === false) {
      props.setCardScore({
        ...props.cardScore,
        sum6: 0,
      });
      props.setHandsUsed({
        ...props.handsUsed,
        sum6: false,
      });
    };
  };

  function setThreeKind(){
    if (props.roll === 0) {
      return;
    }
    for(let i = 0; i <= 1; i++){
      let count = 1;
      for(let j = i + 1; j < diceValuesArray.length; j++){
        if(diceValuesArray[i] === diceValuesArray[j])
          count++;
       }
      if(count >= 3) {
        props.setCardScore({
          ...props.cardScore,
          threeKind: 20,
        });
      };
    };
    props.setHandsUsed({
      ...props.handsUsed,
      threeKind: true,
    });
    if (document.getElementById('threekind').checked === false) {
      props.setCardScore({
        ...props.cardScore,
        threeKind: 0,
      });
      props.setHandsUsed({
        ...props.handsUsed,
        threeKind: false,
      });
    };
  };

  function setFourKind() {
    if (props.roll === 0) {
      return;
    }
    for(let i = 0; i <= 1; i++) {
      let count = 1;
      for(let j = i + 1; j < diceValuesArray.length; j++){
        if(diceValuesArray[i] === diceValuesArray[j])
          count++;
       }
      if(count >= 4) {
        props.setCardScore({
          ...props.cardScore,
          fourKind: 35,
        });
      }
    };
    props.setHandsUsed({
        ...props.handsUsed,
        fourKind: true,
    });
    if (document.getElementById('fourkind').checked === false) {
      props.setCardScore({
        ...props.cardScore,
        fourKind: 0,
      });
      props.setHandsUsed({
        ...props.handsUsed,
        fourKind: false,
      });
    };
  };

  function setFull(){
    let countObj = {};
    for(let x of diceValuesArray){
      countObj[x] = (countObj[x] || 0) + 1;
    };
    let vals = Object.values(countObj);
    if ((vals[0] === 2 && vals[1] === 3) || (vals[1] === 2 && vals[0] === 3)) {
      props.setCardScore({
        ...props.cardScore,
        fullHouse: 25,
      });
    }
    props.setHandsUsed({
        ...props.handsUsed,
        fullHouse: true,
    });
    if (document.getElementById('fullhouse').checked === false) {
      props.setCardScore({
        ...props.cardScore,
        fullHouse: 0,
      });
      props.setHandsUsed({
        ...props.handsUsed,
        fullHouse: false,
      });
    };
  };

  function setTwoPair(){
    let countObj = {};
    for(let x of diceValuesArray){
      countObj[x] = (countObj[x] || 0) + 1;
    };
    let vals = Object.values(countObj);
    if ((vals[0] >= 2 && vals[1] >= 2) || (vals[1] >= 2 && vals[2] >= 2) || (vals[0] >= 2 && vals[2] >= 2)) {
      props.setCardScore({
        ...props.cardScore,
        twoPair: 20,
      });
    };
    props.setHandsUsed({
        ...props.handsUsed,
        twoPair: true,
    });
    if (document.getElementById('twopair').checked === false) {
      props.setCardScore({
        ...props.cardScore,
        twoPair: 0,
      });
      props.setHandsUsed({
        ...props.handsUsed,
        twoPair: false,
      });
    };
  };

  function setSmall(){
    if (diceValuesArray.includes(1) && diceValuesArray.includes(2) && diceValuesArray.includes(3) && diceValuesArray.includes(4)) {
      props.setCardScore({
        ...props.cardScore,
        smallStraight: 30,
      });
    }
    if (diceValuesArray.includes(2) && diceValuesArray.includes(3) && diceValuesArray.includes(4) && diceValuesArray.includes(5)) {
      props.setCardScore({
        ...props.cardScore,
        smallStraight: 30,
      });
    }
    if (diceValuesArray.includes(3) && diceValuesArray.includes(4) && diceValuesArray.includes(5) && diceValuesArray.includes(6)) {
      props.setCardScore({
        ...props.cardScore,
        smallStraight: 30,
      });
    }
    props.setHandsUsed({
        ...props.handsUsed,
        smallStraight: true,
    });
    if (document.getElementById('smallstraight').checked === false) {
      props.setCardScore({
        ...props.cardScore,
        smallStraight: 0,
      });
      props.setHandsUsed({
        ...props.handsUsed,
        smallStraight: false,
      });
    };
  };
  function setLarge(){
    if (diceValuesArray.includes(1) && diceValuesArray.includes(2) && diceValuesArray.includes(3) && diceValuesArray.includes(4) && diceValuesArray.includes(5)) {
      props.setCardScore({
        ...props.cardScore,
        largeStraight: 40,
      });
    }
    if (diceValuesArray.includes(2) && diceValuesArray.includes(3) && diceValuesArray.includes(4) && diceValuesArray.includes(5) && diceValuesArray.includes(6)) {
      props.setCardScore({
        ...props.cardScore,
        largeStraight: 40,
      });
    }
    props.setHandsUsed({
        ...props.handsUsed,
        largeStraight: true,
    });
    if (props.cardScore.largeStraight > 0) {
      props.setCardScore({
        ...props.cardScore,
        largeStraight: 0,
      });
      props.setHandsUsed({
        ...props.handsUsed,
        largeStraight: false,
      });
    };
  };
  function setChance(){
    let diceSum = 0;
    diceSum = diceValuesArray.reduce(function(a, b){
        return a + b;
    }, 0);
    props.setCardScore({
      ...props.cardScore,
      chance: diceSum,
    });
    props.setHandsUsed({
      ...props.handsUsed,
      chance: true,
    });
    if (document.getElementById('chance').checked === false) {
      props.setCardScore({
        ...props.cardScore,
        chance: 0,
      });
      props.setHandsUsed({
        ...props.handsUsed,
        chance: false,
      });
    };
  };
  function setYahtzee(){
    if (props.roll === 0) {
      return;
    }
    for(let i = 0; i <= 1; i++){
      let count = 1;
      for(let j = i + 1; j < diceValuesArray.length; j++){
        if(diceValuesArray[i] === diceValuesArray[j])
          count++;
       }
      if(count === 5) {
         props.setCardScore({
           ...props.cardScore,
           yahtzee: 50,
         });
      }
    };
    props.setHandsUsed({
        ...props.handsUsed,
        yahtzee: true,
    });
    if (document.getElementById('yahtzee').checked === false) {
      props.setCardScore({
        ...props.cardScore,
        yahtzee: 0,
      });
      props.setHandsUsed({
        ...props.handsUsed,
        yahtzee: false,
      });
    };
  };
  function setYahtzee2(){
    if (props.roll === 0) {
      return;
    }
    for(let i = 0; i <= 1; i++){
      let count = 1;
      for(let j = i + 1; j < diceValuesArray.length; j++){
        if(diceValuesArray[i] === diceValuesArray[j])
          count++;
       }
      if(count === 5) {
         props.setCardScore({
           ...props.cardScore,
           bonusYahtzee: 50,
         });
      }
    };
    props.setHandsUsed({
        ...props.handsUsed,
        bonusYahtzee: true,
    });
    if (document.getElementById('yahtzee2').checked === false) {
      props.setCardScore({
        ...props.cardScore,
        bonusYahtzee: 0,
      });
      props.setHandsUsed({
        ...props.handsUsed,
        bonusYahtzee: false,
      });
    };
  };

  return (
    <>
    <form id="score-pad-form">
      <input type="checkbox" id="sum1" onClick={setOnes}/>
      <label for="sum1">Aces - Count and Add Only Aces <span className="display-single-score">{props.cardScore.sum1}</span></label><br />
      <input type="checkbox" id="sum2" onClick={setTwos}/>
      <label for="sum2">Twos - Count and Add Only Twos <span className="display-single-score">{props.cardScore.sum2}</span></label><br />
      <input type="checkbox" id="sum3" onClick={setThrees}/>
      <label for="sum3">Threes - Count and Add Only Threes <span className="display-single-score">{props.cardScore.sum3}</span></label><br />
      <input type="checkbox" id="sum4" onClick={setFours}/>
      <label for="sum4">Fours - Count and Add Only Fours <span className="display-single-score">{props.cardScore.sum4}</span></label><br />
      <input type="checkbox" id="sum5" onClick={setFives}/>
      <label for="sum5">Fives - Count and Add Only Fives <span className="display-single-score">{props.cardScore.sum5}</span></label><br />
      <input type="checkbox" id="sum6" onClick={setSixes}/>
      <label for="sum6">Sixes - Count and Add Only Sixes <span className="display-single-score">{props.cardScore.sum6}</span></label><br />
      <em class="indent-2">Bonus - If you score 63 or more <span className="display-single-score">{props.cardScore.bonus}</span></em>
      <h4>Total - Top Section: <span className="display-single-score-total">{props.cardScore.topTotal}</span></h4>
      <hr />
      <input type="checkbox" id="threekind" onClick={setThreeKind}/>
      <label for="threekind">Three of a Kind <span className="display-single-score">{props.cardScore.threeKind}</span></label><br />
      <input type="checkbox" id="fourkind" onClick={setFourKind}/>
      <label for="fourkind">Four of a Kind <span className="display-single-score">{props.cardScore.fourKind}</span></label><br />
      <input type="checkbox" id="fullhouse" onClick={setFull}/>
      <label for="fullhouse">Full House <span className="display-single-score">{props.cardScore.fullHouse}</span></label><br />
      <input type="checkbox" id="twopair" onClick={setTwoPair}/>
      <label for="twopair">Two Pair <span className="display-single-score">{props.cardScore.twoPair}</span></label><br />
      <input type="checkbox" id="smallstraight" onClick={setSmall}/>
      <label for="smallstraight">Small Straight - Sequence of 4 <span className="display-single-score">{props.cardScore.smallStraight}</span></label><br />
      <input type="checkbox" id="largestraight" onClick={setLarge}/>
      <label for="largestraight">Large Straight - Sequence of 5 <span className="display-single-score">{props.cardScore.largeStraight}</span></label><br />
      <input type="checkbox" id="chance" onClick={setChance}/>
      <label for="chance">Chance - Add total of all dice <span className="display-single-score">{props.cardScore.chance}</span></label><br />
      <input type="checkbox" id="yahtzee" onClick={setYahtzee}/>
      <label for="yahtzee">Yahtzee - 5 of a kind <span className="display-single-score">{props.cardScore.yahtzee}</span></label><br />
      <input type="checkbox" id="yahtzee2" onClick={setYahtzee2}/>
      <label for="yahtzee2">Yahtzee Bonus <span className="display-single-score">{props.cardScore.bonusYahtzee}</span></label><br />
      <h4>Total - Bottom Section: <span className="display-single-score-total">{props.cardScore.bottomTotal}</span></h4>
      <hr />
      <h3>Grand Total: <span className="display-single-score-total">{props.cardScore.grandTotal}</span></h3>
    </form>
    </>
  );
}

export default ScoreCard;
